<template>
  <div  class="cms-list-layout" v-loading="loading">
    <div class="flow">
      <div class="flow__inner">
        <div class="flow__build">
          <div class="flow__icon">
            <img src="../../../assets/images/process-active2.png" alt>
          </div>
          <div class="flow__text">订单生成</div>
          <div class="flow__time">{{$moment(order.orderTime)}}</div>
          <div class="flow__line"></div>
        </div>
        <div class="flow__build">
          <div class="flow__icon" v-if="order.status==2||order.status==3||order.status==4">
            <img src="../../../assets/images/process-active5.png" alt>
          </div>
          <div
            class="flow__icon flow__icon--suc"
            v-if="order.status<2||order.status==5||order.status==6"
          >
            <img src="../../../assets/images/process5.png" alt="1">
          </div>
          <div class="flow__text">支付成功</div>
          <div class="flow__time" v-if="order.payTime">{{$moment(order.payTime)}}</div>
          <div class="flow__time" v-if="!order.payTime">&nbsp;</div>
          <div class="flow__line" v-if="order.status==2||order.status==3||order.status==4"></div>
          <div class="flow__line-inactive" v-if="order.status<2||order.status==5||order.status==6"></div>
        </div>
        <div class="flow__build">
          <div class="flow__icon" v-show="order.status==3||order.status==4">
            <img src="../../../assets/images/process-active1.png" alt>
          </div>
          <div class="flow__icon" v-show="order.status<3||order.status==5||order.status==6">
            <img src="../../../assets/images/process1.png" alt="1">
          </div>
          <div class="flow__text">卖家发货</div>
          <div class="flow__time" v-show="order.deliveryTime">{{$moment(order.deliveryTime)}}</div>
          <div class="flow__time" v-show="!order.deliveryTime">&nbsp;</div>
          <div class="flow__line" v-show="order.status==3||order.status==4"></div>
          <div
            class="flow__line-inactive"
            v-show="order.status<3||order.status==5||order.status==6"
          ></div>
        </div>
        <div class="flow__build flow__build--mar70">
          <div class="flow__icon" v-show="order.status==3||order.status==4">
            <img src="../../../assets/images/process-active3.png" alt>
          </div>
          <div class="flow__icon" v-show="order.status<3||order.status==5||order.status==6">
            <img src="../../../assets/images/process3.png" alt="1">
          </div>
          <div class="flow__text">等待收货</div>
          <div class="flow__time" v-show="order.deliveryTime">{{$moment(order.deliveryTime)}}</div>
          <div class="flow__time" v-show="!order.deliveryTime">&nbsp;</div>
          <div class="flow__line" v-show="order.status==3||order.status==4"></div>
          <div
            class="flow__line-inactive"
            v-show="order.status<3||order.status==5||order.status==6"
          ></div>
        </div>
        <div class="flow__build flow__build--last">
          <div class="flow__icon flow__icon--noh" v-show="order.status==4">
            <img src="../../../assets/images/process-active4.png" alt>
          </div>
          <div class="flow__icon" v-show="order.status<4||order.status==5||order.status==6">
            <img src="../../../assets/images/process4.png" alt="1">
          </div>
          <div class="flow__text">订单完成</div>
          <div class="flow__time" v-show="order.receiveTime">{{$moment(order.receiveTime)}}</div>
          <div class="flow__time" v-show="!order.receiveTime">&nbsp;</div>
        </div>
      </div>
    </div>
    <div class="orderdecs__goods clearfix">
      <div class="goods__orderinfo">
        <h3 class="goods__title">订单信息</h3>
        <ul class="goods__itemsinner" style="padding:5px 15px 15px 15px">
          <li class="goods__item">
            <span class="goods__key">订单编号：</span>
            <span class="goods__value" v-show="!order.visual">{{order.orderCode}}</span>
          </li>
          <li class="goods__item">
            <span class="goods__key">订单类型：</span>
            <span class="goods__value">{{order.orderType|handlerType}}</span>
          </li>
          <li class="goods__item">
            <span class="goods__key">下单用户：</span>
            <span class="goods__value" v-if="order.orderExt">{{order.orderExt.customerName}}</span>
          </li>
          <li class="goods__item">
            <span class="goods__key">支付方式：</span>
            <span class="goods__value">{{order.payMode|handlerPayMode}}</span>
          </li>

          <li class="goods__item" v-if="order.orderTime">
            <span class="goods__key">下单时间：</span>
            <span class="goods__value">{{$moment(order.orderTime)}}</span>
          </li>
          <li class="goods__item" v-if="order.payTime">
            <span class="goods__key">付款时间：</span>
            <span class="goods__value">{{$moment(order.payTime)}}</span>
            <span v-if="!order.payTime">-</span>
          </li>
          <li class="goods__item" v-if="order.deliveryTime">
            <span class="goods__key">发货时间：</span>
            <span class="goods__value">{{$moment(order.deliveryTime)}}</span>
            <span v-if="!order.deliveryTime">-</span>
          </li>
          <li class="goods__item" v-if="order.receiveTime">
            <span class="goods__key">成交时间：</span>
            <span class="goods__value">{{$moment(order.receiveTime)}}</span>
            <span v-if="!order.receiveTime">-</span>
          </li>
        </ul>
        <ul :class="order.orderExt?'goods__itemsinner':''">
          <li class="goods__item" v-if="order.orderExt&&order.orderExt.deliveryCompany">
            <span class="goods__key">配送方式：</span>
            <span class="goods__value goods__value--lh30">{{order.orderExt.deliveryCompany.cnName}}</span>
            <span v-if="order.orderExt&&!order.orderExt.deliveryCompany">-</span>
          </li>
          <li class="goods__item" v-if="order.orderExt&&order.orderExt.expressNum">
            <span class="goods__key">运单编号：</span>
            <span class="goods__value goods__value--lh30">{{order.orderExt.expressNum}}</span>
            <span v-if="order.orderExt&&!order.orderExt.deliveryCompany">-</span>
          </li>
          <li class="goods__item">
            <span class="goods__key">收货地址：</span>
            <span
            id="copy"
              v-if="order.orderExt"
            >{{order.orderExt.customerAddress}} {{order.orderExt.customerTel}} 
            </span>
            <a href="javascript:;" class="blue" @click="copyText" data-clipboard-target="#copy" id="triggerCopy">复制</a>
          </li>
          <li class="goods__item">
            <span class="goods__key">买家留言：</span>
            <span
              class="goods__value goods__value1"
              v-if="order.orderExt"
            >{{order.orderExt.customerTxt}}</span>
            <span v-if="order.orderExt&&!order.orderExt.customerTxt">-</span>
          </li>
        </ul>
        <ul  class="goods__itemsinner goods__itemsinner--noline"
         v-if="order.orderExt&&order.orderExt.needInvoice" >
          <li class="goods__item">
            <span class="goods__key">发票类型：</span>
            <span class="goods__value goods__value--lh30"
              v-if="order.orderExt">
              {{order.orderExt.invoiceType===1?'个人发票':'企业发票'}}
            </span>
            <span v-else>-</span>
          </li>
          <li class="goods__item">
            <span class="goods__key">发票抬头：</span>
            <span
              class="goods__value goods__value--lh30"
              v-if="order.orderExt"
            >{{order.orderExt.invoiceTitle}}</span>        
          </li>
          <li class="goods__item" v-if="order.orderExt.invoiceType!==1">
            <span class="goods__key">纳税人识别号：</span>
            <span class="goods__value goods__value--lh30" >{{order.orderExt.invoiceTaxpayerNum}}</span>
          </li>
        </ul>
      </div>
      <!-- 物流 -->
      <div class="orderstate">
        <div class="orderstate-inner">
          <!-- 标题 -->
          <div>
            <span class="orderstate__title--text">
              <img class="orderstate__title--icon" src="../../../assets/images/order_state.png" alt>
              订单状态
            </span>
            <span
            class="orderstate__title--text"
              :class="order.status==5||order.status==6?'btnDColor':'btnCColor'"
              style="margin-right:10px;"
            >{{order.status|handlerOrderStatus}}</span>
          </div>
          <!--操作按钮 -->
          <div class="orderstate__opt">
            <el-button size="mini" type="primary"    @click="cancelOrder(order.id)"  v-if="order.status==1">取消</el-button>
            <el-button size="mini" type="primary"   @click="printOrder(order.id)"  v-if="order.status<3">打印订单</el-button>
            <el-button size="mini" type="primary"    @click="setChangPrice(order)"  v-if="order.status==1">修改价格</el-button>
            <!-- <el-button size="mini" type="danger"    v-if="order.status>5">删除</el-button> -->
          </div>          
          <!-- 物流信息 -->
           <span @click="getFlowInfo(order.id)" class="orderstate__link"
            v-if="order.status==3||order.status==4"
           >点击查看物流</span>
          <div class="orderstate__scroll" v-loading="expressLoading">
            <div class="orderstate__info" v-for="(flow, index) in flowInfo" :key="index">
              <div class="orderstate__icon">           
                <i class="express-icon"></i>
                <div class="orderstate__icon--line" v-if="index!=flowInfo.length-1"></div>
              </div>
              <div class="orderstate__scrolltext">
                <span class="express-time">{{flow.acceptTime}}</span>
                <span class="express-info" :class="index==0?'bold':''">{{flow.acceptStation}}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 内容列表 -->
    <div class="tablepa">
      <table class="member-table member-table--pos">
        <thead class="member-table__thead">
          <tr>
            <th class="member-table__th member-table__th--color">商品</th>
            <th class="member-table__th member-table__th--color">单价</th>
            <th class="member-table__th member-table__th--color">数量</th>
            <th class="member-table__th member-table__th--color">小计</th>
          </tr>
        </thead>
        <!-- 列表循环 -->
        <tbody>
          <tr class="member-table__tr" v-for="(item, index) in order.items" :key="index">
            <td class="member-table__td member-table__td--nob">
              <div class="member-ordersdecs-product">
                <div class="member-product__media member-product__media--nob">
                  <img
                    :src="$cms.filterUrl(item.productInfo.coverImageUrl)"
                    class="member-product__img"
                  >
                </div>
                <div class="member-product__info member-product__info--width">
                  <a
                    class="order__details__pname"
                    href="javascript:;"
                  >{{item.productInfo.productName}}</a>

                  <div
                    class="member-product__standard member-product__standard--size"
                    v-if="item.productInfo.skuAttr"
                  >
                    <span
                      v-for="(sku, indexs) in JSON.parse(item.productInfo.skuAttr) "
                      :key="indexs"
                    >{{sku.name}}: {{sku.value}}&nbsp;&nbsp;&nbsp;</span>
                  </div>
                </div>
              </div>
            </td>
            <td class="member-table__td member-table__td--nob">
              <span class="member-product__user">￥{{$cms.float(item.productPrice)}}</span>
            </td>
            <td class="member-table__td member-table__td--nob">
              <div class="member-table__inner">
                <p class="member-product__total">{{item.productCount}}</p>
              </div>
            </td>
            <td class="member-table__td member-table__td--nob">
              <div class="member-table__inner">￥{{$cms.float(item.payAmount)}}</div>
            </td>
          </tr>
        </tbody>
        <tbody>
          <tr class="member-table__tr member-table__tr--nob">
            <td class="member-table__td member-table__td--nob"></td>
            <td
              v-if="order.status == 2||order.status == 3||order.status == 4"
              class="member-table__td member-table__td--nob"
            ></td>
            <td class="member-table__td member-table__td--nob member-table__td--over">
              <div class="member-orders__numinner">
                <div class="member-product__state member-product__user--size">商品总额：</div>
                <div class="member-product__state member-product__user--size">运费：</div>
                <div
                  class="container__report--color member-product__state"
                  style="font-size: 14px"
                >实付款：</div>
                <div class="member-product__state member-product__user--size member-product__fa">更多：
                  <div class="member-product__fa">
                    <i class="fa fa-angle-double-down"></i>
                    <div class="member-product__more">
                      <p class="member-product__message member-product__message--pa10">
                        <span class="member-product__style">卖家优惠：</span>
                        <span class="member-product__mon">￥{{$cms.float(order.sellerDiscount)}}</span>
                      </p>
                      <p class="member-product__message">
                        <span class="member-product__style">平台优惠：</span>
                        <span class="member-product__mon">￥{{$cms.float(order.platformDiscount)}}</span>
                      </p>
                      <p class="member-product__message">
                        <span class="member-product__style">积分抵扣：</span>
                        <span class="member-product__mon">￥{{$cms.float(order.integralDiscount)}}</span>
                      </p>
                      <p class="member-product__message">
                        <span class="member-product__style">红包抵扣：</span>
                        <span class="member-product__mon">￥{{$cms.float(order.redPacketDiscount)}}</span>
                      </p>
                      <p class="member-product__message">
                        <span class="member-product__style">返积分：</span>
                        <span class="member-product__mon">{{order.sendIntegral}}</span>
                      </p>
                      <p class="member-product__message">
                        <span class="member-product__style">返红包：</span>
                        <span class="member-product__mon">{{order.sendIntegral}}</span>
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </td>
            <td class="member-table__td member-table__td--nob">
              <div class="member-orders__amountinner">
                <div
                  class="member-table__inner member-product__user--size member-product__state"
                >￥{{$cms.float(order.orderTotalAmount)}}</div>
                <div
                  class="member-table__inner member-product__user--size member-product__state"
                >￥{{$cms.float(order.orderFreight)}}</div>
                <div
                  class="member-table__inner member-product__user--size member-product__state member-product__user--bsize"
                >￥{{$cms.float(order.orderPayAmount)}}</div>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <change-price ref="changePrice" @change="updateList"></change-price>
    <print-page ref="print" style="display:none"></print-page>
  </div>
</template>

<script>
import { orderGet, getFlowInfo, cancelOrder } from './../../../api/shop/order.js'
import changePrice from './changePrice.vue'
import Clipboard from 'clipboard'
import printPage from './shipBill.vue'
export default {

  data() {
    return {
      order: {},
      expressLoading: false,
      flowInfo: []
    }
  },
  components: {
    changePrice,
    printPage
  },
  methods: {
    copyText() {
      var clipboard = new Clipboard('#triggerCopy')
      const that = this
      clipboard.on('success', function(e) {
        that.$message({
          message: '复制成功',
          type: 'success'
        })
        e.clearSelection()
      })
    },
    updateList(status) {
      if (status) {
        this.getOrderInfo(this.$route.query.id)
      }
    },
    setChangPrice(item) {
      this.$refs.changePrice.setChangPrice(item)
    },
    cancelOrder(orderId) {
      this.$confirm('是否确认取消该订单', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(res => {
          cancelOrder({ orderId: orderId }).then(res => {
            if (res.code === 200) {
              this.$successMessage('取消成功')
              this.getOrderInfo(this.$route.query.id)
            } else {
              this.$errorMessage(res.message)
            }
          })
        })
        .catch(() => {})
    },
    printOrder(id) {
      this.$refs.print.getOrderInfo(id).then(res => {
        var printStr = res.innerHTML
        let printUrl = ''
        if (process.env.NODE_ENV === 'production') {
          const path = location.pathname.substring(0, location.pathname.lastIndexOf('/'))
          printUrl = path + '/static/print/print.html'
        } else {
          printUrl = '/static/print/print.html'
        }
        var page = window.open(printUrl, '')
        setTimeout(() => {
          page.document.body.innerHTML = printStr
          return false
        }, 50)
      })
    },
    getOrderInfo(id) {
      this.loading = true
      // 获取订单信息
      orderGet({ id: id }).then(res => {
        if (res.code === 200) {
          this.order = res.data
          this.loading = false
        }
      })
    },
    getFlowInfo(orderId) {
      if (this.flowInfo.length <= 0) { // 不要一直去请求
        this.expressLoading = true
        getFlowInfo({ orderId: orderId }).then(res => {
          if (res.code === 200) {
            if (res.data.traces.length > 0) {
              this.flowInfo = res.data.traces.reverse() // 翻转物流信息
            } else {
              this.flowInfo = [{
                acceptTime: '',
                acceptStation: '暂无物流信息'
              }]
            }
          } else {
            this.$errorMessage(res.message)
          }
          this.expressLoading = false
        })
      }
    }
  },
  filters: {
    handlerOrderStatus(val) {
      // 交易状态的过滤器
      var orderStatus = [
        '',
        '待付款',
        '待发货',
        '待收货',
        '已完成',
        '已取消',
        '超时交易关闭'
      ]
      return orderStatus[parseInt(val)]
    },
    handlerType(val) {
      // 订单类型的过滤器
      return val === 1 ? '实物订单' : '虚拟订单'
    },
    handlerSource(val) {
      // 订单来源的过滤器
      var sources = ['', 'PC', '手机端', 'APP', '公众号', '小程序', '平板']
      return sources[parseInt(val)]
    },
    handlerPayMode(val) {
      // 处理支付方式
      var payModes = ['', '支付宝', '微信', '银联', '余额']
      return payModes[parseInt(val)]
    },
    handlerRefundStatus(val) {
      // 售后处理状态的过滤器
      var refundStatus = [
        '待卖家确认',
        '退款中',
        '卖家拒绝',
        '',
        '待买家发货',
        '待卖家收货',
        '待买家收货',
        '待卖家发货',
        '已完成',
        '买家取消'
      ]
      return refundStatus[parseInt(val)]
    }
  },
  created() {
    this.getOrderInfo(this.$route.query.id)
  }
}
</script>

 <style lang="scss" scoped>
 .blue{
   color: #43AAD7;
   display: inline-block;
   width: 30px;
   text-align: center;
 }
</style>



